@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,500;0,700;1,400&display=swap');

.gradient-text {
    background-color: #92e;
    background-image: linear-gradient(45deg, #92e, #ff3366);
    background-size: 100%;
    background-clip: text;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
}

h1, .c-title, .c-subtitle, .c-text {
    font-family: 'Poppins', sans-serif; /*GT Walsheim Pro Bold*/
    font-weight: bold;
    color: white;
}

.c-text {
    font-weight: normal;
}

.card-custom {
    border-radius: 20px;
    background-color: hsl(0, 0%, 12%);
}

.icon-add {
    cursor: pointer;
    position: relative;
    top: -6px;
    width: 24px;
    height: 24px;
}

.icon-edit {
    cursor: pointer;
    /*position: relative;
    top: -3px;
