@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,500;0,700;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,500;0,700;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*@import url('https://db.onlinewebfonts.com/c/f7afe66fe9d022d7dc7fd57e4adc078f?family=GT+Walsheim+Pro+Bold');*/

.gradient-text {
    background-color: #92e;
    background-image: linear-gradient(45deg, #92e, #ff3366);
    background-size: 100%;
    background-clip: text;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
}

.d-flex {
    height: 7vh;
}

.header-nav {
    border-bottom: 1px solid rgba(68, 68, 68, 0.3);
}

#page-logo {
    width: 60px;
    height: 60px;
    position: relative;
    top: 2px;
}

.logo h3 {
    font-family: 'Poppins', sans-serif; /*GT Walsheim Pro Bold*/
    font-weight: bold;
    position: relative;
    top: 8px;
}

#studio {
    color: white;
}

.navlinks {
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    font-size: 14px;
    color: #777;
    position: relative;
    top: 8px;
    cursor: pointer;
}

.navlinks:hover {
    color: #999;
}

.signature {
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    font-size: 18px;
    color: white;

    cursor: pointer;
}

.modal-title {
    font-family: 'Poppins', sans-serif; /*GT Walsheim Pro Bold*/
    font-weight: bold;
}

.modal-text {
    font-family: 'Poppins', sans-serif; /*GT Walsheim Pro Bold*/
    font-weight: regular;
    color: white;
}

.round-input-box {
    border-radius: 20px;
    box-shadow: none !important;
    border: none;
}

.round-input-box:focus {
    border-radius: 20px;
    box-shadow: none !important;
    border: none;
}

.modal-full {
    background-color: hsl(0, 0%, 8%);
    border-bottom: 1px solid rgba(68, 68, 68, 0.3);
}

.close-btn, .submit-btn {
    border-color: #92e !important;
    background-color: #92e !important;
}

.close-btn:hover, .close-btn:active, .close-btn:focus, .submit-btn:hover,  .submit-btn:active,  .submit-btn:focus {
    border-color: #ff3366 !important;
    background-color: #ff3366 !important;
    box-shadow: none !important;
}

.modal-foot {
    border-top: none;
    background-color: hsl(0, 0%, 8%);
}
.gradient-text {
    background-color: #92e;
    background-image: linear-gradient(45deg, #92e, #ff3366);
    background-size: 100%;
    background-clip: text;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
}

h1, .c-title, .c-subtitle, .c-text {
    font-family: 'Poppins', sans-serif; /*GT Walsheim Pro Bold*/
    font-weight: bold;
    color: white;
}

.c-text {
    font-weight: normal;
}

.card-custom {
    border-radius: 20px;
    background-color: hsl(0, 0%, 12%);
}

.icon-add {
    cursor: pointer;
    position: relative;
    top: -6px;
    width: 24px;
    height: 24px;
}

.icon-edit {
    cursor: pointer;
    /*position: relative;
    top: -3px;
    left: 20px;*/
    width: 16px;
    height: 16px;
}

.icon-trash {
    cursor: pointer;
    /*position: relative;
    top: -3px;
    left: 25px;*/
    width: 16px;
    height: 16px;
}

.spacer {
    visibility: hidden;
}
body {
    background-color: hsl(0, 0%, 8%);
}
